.logo-wrapper{
  background: #004d80;
  display: flex;
  justify-content:space-between;
  max-width: 100%;
  font-size: 15px;
  gap: 20px;
  padding: 20px 50px;
  align-content: center;
  align-items: center;
  border-bottom: 3px solid #3366ff;
  color: #fff;
}
.input-Wrapper input{
  display: block;
  margin: 0 auto;
  padding: 0.5rem 1.5rem;
  outline: 0;
  border: 0;
}
.input-Wrapper label{
  display: block;
  text-align: center;
  padding: 1rem 1rem;
  font-size: 20px;
}
.opt-heading{
  display: block;
  text-align: center;
  font-size: 20px;
  padding:1rem 0;
  
}
.opt-ans button{
  margin: 20px 0 0 0;
  padding: 10px 15px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  outline: none;
  border-radius: 20px;
  border: 2px solid #004d80;
  background: #004d80;
}
.opt-ans button:hover {
    background: #fff;
    color: #2c0b8e
} 

   

.predict-page h1{
  margin: 0 0 0 3rem;
}

.opt-ans{
  display: flex;
  gap:1rem;
  padding: 1rem 6rem;
  max-width: 100%;
  margin: 0 auto;
  justify-content: space-evenly;
  
}
.btnARQ, .btnGRA{
  display: block;
  width: 240px;
  font-size: 12px;
  margin: 5px auto 0 auto;
  padding: 10px 15px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  outline: none;
  background: #004d80;
  border: 2px solid #004d80;

  

}
.btnAA{
  display: block;
  width: 240px;
  font-size: 20px;
  padding: 0.3rem 0;
  margin: 0 auto;
  padding: 10px 15px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  outline: none;
  background: #004d80;
  border: 2px solid #004d80;
}
.btn-ans{
  padding: 2rem 3rem;
}
.btn-ans button:hover{
  background: #fff;
   color: #2c0b8e
}
.predict-list{
  list-style:decimal;
  max-width: 100%;

 
}
.predict-list li{
  padding: 0.2rem 0;
  border-bottom: 1px solid;
  max-width: 230px;
  margin: 0 auto;
}

.predict-list li.active {
  color: #fff;
}

.predict-page{
  max-width: 100%;
  text-align: center;
}

a{
  text-decoration: none;
}


